import React from "react"
import { graphql } from "gatsby"
import ReactMarkdown from "react-markdown"

import Layout from "../components/layout"

export const query = graphql`  
  query ProjectsPageQuery($id: String!) {
    strapiPages(id: { eq: $id }) {
      id
      title,
      slug,
      content
    }
    allStrapiProjects {
      edges {
        node {
          name,
          description,
          slug
        }
      }
    }
  }
`

const Projects = ({ data }) => {
  const page = data.strapiPages;
  const projects = data.allStrapiProjects;
  return (
    <Layout>
        <h2>{page.title}</h2>
        <div>
            <ReactMarkdown source={page.content} />
          <div >
            <ul>
              {projects.edges.map(project => (
                <li key={project.node.slug} >{project.node.name}</li>
              ))}
            </ul>
          </div>
        </div>
    </Layout>
  )
}

export default Projects  